@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rosario:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
*{margin:0px;padding:0px}body{min-height:100vh}.container{min-height:100vh;font-family:"Quicksand";font-weight:400}.container h1{color:#8faac3;text-align:center;height:80px;padding:.5em;font-size:80px}@media screen and (max-width: 480px){.container h1{font-size:45px}}.container section{min-height:850px;width:100%;background-image:linear-gradient(45deg, #30cfd0 0%, #330867 100%);font-family:"Rosario";display:flex;flex-direction:column;justify-content:space-around;align-items:center}.container section button{width:200px;height:100px;font-size:40px;margin:10px;border-radius:20px;background-color:#fff}.container footer{color:#8faac3;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;height:80px;font-size:20px}.container footer a{color:inherit;text-decoration:none}.question{min-height:200px;width:80%;background-image:linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);border-radius:30px;margin:30px 0px;font-family:"Rosario",serif}.question *{margin:5px 40px;color:#080c3b;background-color:inherit}.question h3{font-size:30px}.question div{font-size:30px}.question div input{width:20px;height:20px}@media screen and (max-width: 480px){.question{padding:10px}.question *{margin-left:10px;text-align:justify}.question h3{font-size:20px}.question div{font-size:15px}.question div label{margin-left:-40px}.question div input{width:15px;height:15px}}.result{display:grid;height:600px;width:1000px;background:#fff;border-radius:30px;grid-template-columns:1fr 1fr;place-items:center}.result .label{display:grid;grid-template-columns:150px 250px;grid-template-rows:repeat(3, 1fr);place-items:center;height:600px;width:400px}.result .label .box{background:blue;height:75px;width:75px;place-self:center end}.result .label .box1{background:red}.result .label .box2{background:#00a3ff}.result .label .box3{background:#e7de0e}.result .label .tag1,.result .label .tag2,.result .label .tag3{display:grid;place-items:center;font-size:30px;height:100px;width:300px}.result svg{height:500px;width:500px;border-radius:30px;margin-right:150px}@media screen and (max-width: 480px){.result{width:350px;height:650px;margin:10px 0;display:flex;flex-direction:column}.result .label{height:300px;width:100%}.result .label .box{height:50px;width:50px;place-self:center}.result .label .tag1,.result .label .tag2,.result .label .tag3{display:grid;place-self:start;place-items:center start;font-size:25px;height:100px;width:200px}.result svg{height:300px;width:300px;margin:20px 0px}}
